<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row>
                <el-col :span="8">
                    <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getOrderList">
                        <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
                    </el-input>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="orderList" border stripe>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column label="" prop="order_number"></el-table-column>
                <el-table-column label="" prop="order_price"></el-table-column>
                <el-table-column label="" >
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
                        <el-tag type="danger" v-else></el-tag>
                    </template> 
                </el-table-column>
                <el-table-column label="" prop="is_send"></el-table-column>
                <!--  -->
                <el-table-column label="" >
                    <template  slot-scope="scope">
                        {{scope.row.create_time |dateFormat}}
                    </template>
                </el-table-column>
                <el-table-column label="" >
                    <template>
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"></el-button>
                        <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
             <el-pagination @size-change="handleSizeChange"  @current-change="handleCurrentChange"
             :current-page="queryInfo.pagenum" :page-sizes="[10, 15, 20, 25]"
             :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper"
             :total="total">
            </el-pagination>
        </el-card> 
         <!--  -->
        <el-dialog title="" :visible.sync="addressVisible" width="50%" @close="addressDialogClosed">
            <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
                <el-form-item label="/" prop="address1">
                    <el-cascader v-model="addressForm.address1" :options="cityData" :props="{ expandTrigger: 'hover',...cityDataProps }"  @change="handleChange"></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="address2">
                  <el-input v-model="addressForm.address2"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="addressVisible = false"> </el-button>
              <el-button type="primary" @click="addressVisible = false"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="progressVisible" width="50%" @close="progressDialogClosed">
            <el-timeline>
                 <!-- Timline -->
                 <el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.time">
                   {{activity.context}}
                 </el-timeline-item>
            </el-timeline>
            
        </el-dialog>    
    </div>
</template>

<script>
// 
import cityData from './citydata'
export default {
    created(){
        this.getOrderList();
    },
    data(){
        return{
            // 
            orderList:[],
            // 
            total:0,
            // 
            queryInfo:{
                query:'',
                pagenum:1,
                pagesize:10,
            },
            // 
            addressVisible:false,
            // 
            addressForm:{
                address1:[],
                address2:'',
            },
            // 
            addressFormRules:{
                address1:[{ required: true, message: '', trigger: 'blur' },],
                address2:[{ required: true, message: '', trigger: 'blur' },],
            },
            // Vue
            cityData:cityData,  
            // 
            cityDataProps:{
                label:'label',
                value:'value',
                children:'children'
            },
            // 
            progressVisible:false,  
            // 
            progressInfo:[
                {
                  "time": "2018-05-10 09:39:00",
                  "ftime": "2018-05-10 09:39:00",
                  "context": ",,",
                  "location": ""
                },
                {
                  "time": "2018-05-10 08:23:00",
                  "ftime": "2018-05-10 08:23:00",
                  "context": "[]  95338",
                  "location": ""
                },
                {
                  "time": "2018-05-10 07:32:00",
                  "ftime": "2018-05-10 07:32:00",
                  "context": " []",
                  "location": ""
                },
                {
                  "time": "2018-05-10 02:03:00",
                  "ftime": "2018-05-10 02:03:00",
                  "context": "[], []",
                  "location": ""
                },
                {
                  "time": "2018-05-09 23:05:00",
                  "ftime": "2018-05-09 23:05:00",
                  "context": " []",
                  "location": ""
                },
                {
                  "time": "2018-05-09 21:21:00",
                  "ftime": "2018-05-09 21:21:00",
                  "context": "[], []",
                  "location": ""
                },
                {
                  "time": "2018-05-09 13:07:00",
                  "ftime": "2018-05-09 13:07:00",
                  "context": " ",
                  "location": ""
                },
                {
                  "time": "2018-05-09 12:25:03",
                  "ftime": "2018-05-09 12:25:03",
                  "context": "",
                  "location": ""
                },
                {
                  "time": "2018-05-09 12:22:24",
                  "ftime": "2018-05-09 12:22:24",
                  "context": "HLA",
                  "location": ""
                },
                {
                  "time": "2018-05-08 21:36:04",
                  "ftime": "2018-05-08 21:36:04",
                  "context": "",
                  "location": ""
                }
            ],
        }
    },
    methods:{
        // 
        async getOrderList(){
            const {data:res} = await this.$http.get('orders',{params:this.queryInfo});
            if(res.meta.status!==200) return this.$message.error('');
            this.orderList = res.data.goods;
            this.total = res.data.total;
            console.log(this.orderList);
        },
        // 
        handleSizeChange(newpagesize){
            this.queryInfo.pagesize = newpagesize;
            this.getOrderList();
        },
        // 
        handleCurrentChange(newpagenum){
            this.queryInfo.pagenum = newpagenum;
            this.getOrderList();
        },
        // 
        showBox(){
            this.addressVisible=true;
        },
        // 
        handleChange(){
            console.log(this.addressForm)
        },
        // 
        addressDialogClosed(){
            this.$refs.addressFormRef.resetFields();
        },
        // 
        async showProgressBox(){
            // const {data:res} = await this.$http.get('/kuaidi/804909574412544580');
            // if(res.meta.status!==200){
            //     return this.$message.error('');
            // }
            // this.progressInfo = res.data;
            this.progressVisible = true;
            // console.log(this.progressInfo);
            
        },
        // 
        progressDialogClosed(){},
    },
}
</script>

<style lang="less" scoped>
// TimeLineTimeLine
    @import '../../plugins/timeline/timeline.css';
    @import '../../plugins/timeline-item/timeline-item.css';
    .el-cascader{
        width: 100%;
    }
</style>